@import url(https://fonts.googleapis.com/css?family=Paytone+One);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.loader-container {
    display: flex;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    background: #262626;
    justify-content: center;
    align-items: center;
    z-index: 20;
    width: 100vw;
    height: 100vh;
    text-align: center;
}

.loader {
    color: white;
    letter-spacing: 0.8vw;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    position: relative;
    -webkit-animation: rotate 2s linear forwards infinite;
            animation: rotate 2s linear forwards infinite;
}

.ellipse {
    position: absolute;
    width: 14px;
    height: 14px;
    border-radius: 50%;
}

.ellipse:first-child {
    background-color: #06addb;
    left: 25px;
    top: -6px;
}

.ellipse:nth-child(2) {
    background-color: #f67676;
    left: 55px;
    top: 16px;
}

.ellipse:nth-child(3) {
    background-color: #8465ac;
    right: 55px;
    top: 16px;
}

.ellipse:nth-child(4) {
    background-color: #f99931;
    bottom: 0px;
    left: 5px;
}

.ellipse:nth-child(5) {
    background-color: #6fbb2d;
    right: 5px;
    bottom: 0;
}

@-webkit-keyframes rotate {
    0% {
        transform: rotate(0deg) scale(1);
    }
    25% {
        transform: rotate(90deg) scale(1.5);
    }
    50% {
        transform: rotate(180deg) scale(1);
    }
    75% {
        transform: rotate(270deg) scale(1.5);
    }
    100% {
        transform: rotate(360deg) scale(1);
    }
}

@keyframes rotate {
    0% {
        transform: rotate(0deg) scale(1);
    }
    25% {
        transform: rotate(90deg) scale(1.5);
    }
    50% {
        transform: rotate(180deg) scale(1);
    }
    75% {
        transform: rotate(270deg) scale(1.5);
    }
    100% {
        transform: rotate(360deg) scale(1);
    }
}

.loader p {
    margin-top: 2vh;
}
.App {
  text-align: center;
}

.App-logo {
  height: 60px;
  float: left;
}

.App-header {
  background-color: #222;
  height: 100px;
  /* padding: 5px; */
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: white;
  float: left;
}

.App-intro {
  font-size: large;
}

.navbar {
  margin-top: auto;
}
.FormField__Link {
  color: #66707d;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 5px;
}
#top-filler {
  margin-bottom: auto;
}

#nav-container {
  align-items: flex-end;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

